.detail {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.background {
  filter: brightness(30%);
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
}
.detail__content {
  position: absolute;
  background-color: white;
  width: 770px;
  min-height: 380px;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 40px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  display: flex;
  justify-content: flex-start;
}

.background img {
  width: auto;
  height: 100%;
  overflow: hidden;
}
.detail__content img {
  position: relative;
  top: -50px;
  width: 230px;
  height: 345px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.detail__link {
  text-decoration: none;
  color: #adaeb9;
  text-transform: uppercase;
  font-size: 20px;
  text-align: center;
  font-weight: 600;
}
